<script>

export default {

  data() {
    return {
      user:{
        email: '',
        pwd: '',
        timeout: null,
      },
    }
  },
  methods: {
    signin() {
      const api = `${process.env.SocketIO}`;
      const vm = this;
      this.$http.post(api, vm.user).then((res) => {
        if(res.data.success){
          //const token = res.data.token;
          //document.cookie = `trafficToken=${token} max-age=`;//把cookie寫入
          vm.$router.push({name: 'Admin'});
        }
      });
    }
  }
}
</script>


<template>

<div class="container">

  <form @submit.prevent="signin">
    <div class="mb-3">
      <label for="exampleInputEmail1" class="form-label">Email address</label>
      <input type="email" name="email" v-model="user.email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" autocomplete="username">
    <div id="emailHelp" class="form-text">We'll never share your email with anyone else.</div>
  </div>
  <div class="mb-3">
    <label for="exampleInputPassword1" class="form-label">Password</label>
    <input type="password" name="pwd" v-model="user.pwd" class="form-control" id="exampleInputPassword1" autocomplete="current-password">
  </div>
  <div class="mb-3 form-check">
    <input type="checkbox" class="form-check-input" id="exampleCheck1">
    <label class="form-check-label" for="exampleCheck1">Check me out</label>
  </div>
  <button type="submit" class="btn btn-primary">Submit</button>
  </form>

</div>

</template>
